.card_hover_container {
  position: absolute;
  top: 0;
  z-index: 4;
  opacity: 0;
  background-color: rgba(7, 5, 14, 0.8);
  border-radius: 7px;
  height: 101%;
  width: 100%;
  transition: 0.4s;
}

.card_name_container {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.card_container {
  max-width: 181px;
  min-width: 150px;
  max-height: 315px;
  min-height: 230px;
  flex: 1;
}

.card_container:hover .card_hover_container {
  opacity: 1;
}

.card_container:hover .card_image_container {
  transform: scale(1.05);
}

.card_image_container {
  max-height: 277px;
  min-height: 233px;
  position: relative;
  transition: 0.4s;
}

.card_image {
  width: 100%;
  height: 100%;
  margin: 0;
}

.card_image > img {
  display: block;
  width: 100%;
  height: fit-content;
  border-radius: 8px;
}

.card_badge {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  color: #ffffff;
  font-weight: 600;
  font-size: 12px;
  height: 20px;
  top: 4px;
  left: -4px;
  border-radius: 8px;
  padding: 0 4px;
  z-index: 5;
}

.card_badge_ivi {
  box-shadow: 0 4px 12px rgb(255 84 46 / 40%);
  background-color: #ea003d;
}

.card_name_container p {
  margin: 0;
}

.movie_name {
  color: #d9d7e0;
  line-height: 20px;
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  overflow: hidden;
  vertical-align: middle;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-top: 8px;
}

.movie_padpiska {
  color: #a5a1b2;
  font-size: 13px;
  font-weight: 500;
  line-height: 16px;
}

.card_grade_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.progress_bars_container {
  display: flex;
  flex-direction: column;
  gap: 0;
  width: 60px;
  align-self: flex-end;
}

.hover_container_left {
  z-index: 1;
  position: absolute;
  left: 16px;
  right: 16px;
  bottom: 16px;
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  font-style: normal;
  color: #d9d7e0;
}

.progress_bar {
  margin: 0;
  width: 1000px;
  font-size: 0;
}

.card_grade_container p {
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
  line-height: 24px;
  height: 24px;
  font-style: normal;
  color: #ffffff;
  margin: 0;
}

.ant-progress {
  font-size: 0;
}

.ant-progress-small:where(
    .css-dev-only-do-not-override-j0nf2s
  ).ant-progress-line,
.ant-progress-small:where(
    .css-dev-only-do-not-override-j0nf2s
  ).ant-progress-line
  .ant-progress-text
  .anticon {
  font-size: 0;
}

.ant-progress-line {
  margin-bottom: 4px;
}

.syuzet {
  display: flex;
  flex-direction: column;
  font-size: 13px;
  line-height: 16px;
  font-weight: 500;
  color: #a5a1b2;
  margin: 5px 0;
}

.syuzet p {
  margin: 0 0 4px 0;
}

.card_image_container:hover &.card_hover_container {
  opacity: 1;
}

.card_hover_country {
  margin-top: 4px;
  display: block;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  color: #d9d7e0;
}

.card_hover_duration {
  margin: 0;
}

.hover_container_right {
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  z-index: 2;
  position: absolute;
  right: 8px;
  top: 8px;
  gap: 7px;
}

.hover_icon {
  font-size: 25px;
  color: #ffffff;
}
